<template>
  <div v-hotkey="keymap">
    <button type="button" title="(N) Change to note insertion mode" @click="selectMode(0)" :class="{selected: selectedModeIdx === 0}" class="switch-button left">Note</button>
    <button type="button" title="(R) Change to rest insertion mode" @click="selectMode(1)" :class="{selected: selectedModeIdx === 1}" class="switch-button">Rest</button>
    <button type="button" title="(B) Change to bar insertion mode" @click="selectMode(2)" :class="{selected: selectedModeIdx === 2}" class="switch-button right">Bar</button>
  </div>
</template>

<script>


  export default {
    name: "ModeSelector",
    props: ['active'],
    data() {
      return {
        selectedModeIdx: 0
      }
    },
    computed: {
      modes() {
        return ["note", "rest", "bar"];
      },
      modeCount() {
        return 3;
      },
      keymap() {
        return {
          'n': () => this.selectMode(0),
          'r': () => this.selectMode(1),
          'b': () => this.selectMode(2),
        }
      },
    },
    methods: {
      selectMode(idx) {
        this.selectedModeIdx = idx;
        this.$emit("change", this.modes[idx]);
      }
    },
    watch: {
      active(newVal) {
        this.selectedModeIdx = this.modes.indexOf(newVal);
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../style/main";

</style>
