<template>
  <div id="app" v-hotkey.stop="keymap">
    <div id="score-container">
      <canvas class="score" id="score-canvas" :class="canvasClasses" />
    </div>
    <div class="controls">
      <div class="score-settings">
        <div class="header">
          <h2>Score</h2>
        </div>
        <div class="audio">
          <AudioPlayer :src="audioFileUrl" :maxPlayCount="maxPlayCount" />
        </div>
        <div>
          <div class="field key-selection" v-if="canChangeKey">
            <label style="padding-top: 1.25rem;">Key signature</label>
            <KeySelector :key-signature="score.key" @change="changeKeySignature" />
          </div>
          <div class="field time-selection" v-if="canChangeTime">
            <label style="padding-top: .5rem;">Time signature</label>
            <TimeSelector :time-signature="score.time" @change="changeTimeSignature" />
          </div>
        </div>
      </div>
      <div class="note-insert">
        <div class="header" style="padding: 0;">
          <h2 style="padding: 1rem; display: inline-block;">
            <a @click="toggleInteractionMode" :class="{ active:(interactionMode === 'insert') }">Insert</a>
            &nbsp;
            <a @click="toggleInteractionMode" :class="{ active:(interactionMode === 'modify') }">Modify</a>
          </h2>
          <div style="float:right;" v-if="selectedNote">
            <button type="button" title="(left) Previous note" class="icon-button" @click="selectPreviousNote"><IconBack /></button>
            <button type="button" title="(right) Next note" class="icon-button" @click="selectNextNote"><IconForward /></button>
            <button type="button" title="(up) Move note up" class="icon-button" @click="moveNoteUp"><IconUp /></button>
            <button type="button" title="(down) Move note down" class="icon-button" @click="moveNoteDown"><IconDown /></button>
            <button type="button" title="(del) Delete selected note" class="icon-button" @click="deleteSelectedNote()"><IconDelete /></button>
          </div>
        </div>

        <div class="note-edit-content">
          <div v-if="voiceCount > 1" style="padding-bottom: 1rem; margin-bottom: 1rem; border-bottom: solid 1px #ffffff22">
            <VoiceSelector :voice-count="voiceCount" @change="selectVoice" />
          </div>
          <div style="padding-bottom: 1rem;">
            <ModeSelector :active.sync="insertionMode" @change="changeInsertionMode" />
          </div>
          <div v-show="insertionMode==='note'" style="padding-bottom: 1rem;">
            <button type="button" title="(1) Semibreve" :class="{active:activeNoteLength===1}" class="icon-button" @click="changeNoteLength(1, 'note')"><IconWhole /></button>
            <button type="button" title="(2) Minim" :class="{active:activeNoteLength===2}" class="icon-button" @click="changeNoteLength(2, 'note')"><IconHalf /></button>
            <button type="button" title="(3) Crotchet" :class="{active:activeNoteLength===4}" class="icon-button" @click="changeNoteLength(4, 'note')"><IconQuarter /></button>
            <button type="button" title="(4) Quaver" :class="{active:activeNoteLength===8}" class="icon-button" @click="changeNoteLength(8, 'note')"><IconEighth /></button>
            <button type="button" title="(5) Semiquaver" :class="{active:activeNoteLength===16}" class="icon-button" @click="changeNoteLength(16, 'note')"><IconSixteenth /></button>
            <div class="dot-block">
              <button type="button" class="icon-button" :class="{active:insertWithDot}" @click="toggleDotForInsertion">Dot</button>
            </div>
          </div>
          <div v-show="insertionMode==='rest'" style="padding-bottom: 1rem;">
            <button type="button" title="(1) Semibreve rest" :class="{active:activeNoteLength===1}"  class="icon-button" @click="changeNoteLength(1, 'rest')"><IconWholeRest /></button>
            <button type="button" title="(2) Minim rest" :class="{active:activeNoteLength===2}"  class="icon-button" @click="changeNoteLength(2, 'rest')"><IconHalfRest /></button>
            <button type="button" title="(3) Crotchet rest" :class="{active:activeNoteLength===4}"  class="icon-button" @click="changeNoteLength(4, 'rest')"><IconQuarterRest /></button>
            <button type="button" title="(4) Quaver rest" :class="{active:activeNoteLength===8}"  class="icon-button" @click="changeNoteLength(8, 'rest')"><IconEighthRest /></button>
            <button type="button" title="(5) Semiquaver rest" :class="{active:activeNoteLength===16}" class="icon-button" @click="changeNoteLength(16, 'rest')"><IconSixteenthRest /></button>
            <div class="dot-block">
              <button type="button" class="icon-button" :class="{active:insertWithDot}" @click="toggleDotForInsertion">Dot</button>
            </div>
          </div>
          <div v-show="insertionMode==='note'" v-if="selectedNote">
            <button type="button" class="icon-button" :class="{active:selectedNote.accidental === 'bb'}" @click="setAccidentalForSelectedNote('bb')"><IconFlat /><IconFlat /></button>
            <button type="button" class="icon-button" :class="{active:selectedNote.accidental === 'b'}" @click="setAccidentalForSelectedNote('b')"><IconFlat /></button>
            <button type="button" class="icon-button" :class="{active:selectedNote.accidental === 'n'}" @click="setAccidentalForSelectedNote('n')"><IconNatural /></button>
            <button type="button" class="icon-button" :class="{active:selectedNote.accidental === '#'}" @click="setAccidentalForSelectedNote('#')"><IconSharp /></button>
            <button type="button" class="icon-button" :class="{active:selectedNote.accidental === '##'}" @click="setAccidentalForSelectedNote('##')"><IconDoubleSharp /></button>
          </div>
          <div v-show="insertionMode==='bar'">
            <button type="button" title="Single bar" :class="{active:insertionMode==='bar'}" class="icon-button" @click.self="selectBar('SINGLE')">Bar</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Vex from 'vexflow';
  import _ from 'lodash';
  import {Score} from './model'
  import {liduchan999, emptyScore} from "./sample-models";
  import {keyIdxToKey, keyToKeyIdx, withinBounds, canvasLocToKey, targetLocFromTouchEvent} from "./utils";
  import VoiceSelector from "./components/VoiceSelector";
  import IconWhole from './assets/icon_whole.svg';
  import IconHalf from './assets/icon_half.svg';
  import IconQuarter from './assets/icon_quarter.svg';
  import IconEighth from './assets/icon_eighth.svg';
  import IconSixteenth from './assets/icon_sixteeth.svg';
  import IconWholeRest from './assets/icon_wholerest.svg';
  import IconHalfRest from './assets/icon_halfrest.svg';
  import IconQuarterRest from './assets/icon_quarterrest.svg';
  import IconEighthRest from './assets/icon_eighthrest.svg';
  import IconSixteenthRest from './assets/icon_sixteethrest.svg';
  import IconSharp from './assets/icon_sharp.svg';
  import IconDoubleSharp from './assets/icon_doublesharp.svg';
  import IconFlat from './assets/icon_flat.svg';
  import IconNatural from './assets/icon_natural.svg';
  import IconUp from './assets/icon_up.svg';
  import IconDown from './assets/icon_down.svg';
  import IconDelete from './assets/icon_delete.svg';
  import IconForward from './assets/icon_forward.svg';
  import IconBack from './assets/icon_back.svg';
  import IconAutoSelect from './assets/icon_autoselect.svg';
  import AudioPlayer from "./components/AudioPlayer";
  import ModeSelector from "./components/ModeSelector";
  import KeySelector from "./components/KeySelector";
  import TimeSelector from "./components/TimeSelector";

  const VF = Vex.Flow

  export default {
    name: 'App',
    components: {
      TimeSelector,
      KeySelector,
      AudioPlayer,
      VoiceSelector,
      IconWhole,
      IconHalf,
      IconQuarter,
      IconEighth,
      IconSixteenth,
      IconWholeRest,
      IconHalfRest,
      IconQuarterRest,
      IconEighthRest,
      IconSixteenthRest,
      IconSharp,
      IconDoubleSharp,
      IconFlat,
      IconNatural,
      IconUp,
      IconDown,
      IconDelete,
      IconBack,
      IconForward,
      IconAutoSelect,
      ModeSelector
    },
    data() {
      return {
        answerText: "N/A",
        answerElement: null,
        answerElementId: null,
        clef: {value: "treble", label: "Treble"},
        selectedNote: null,
        hoveredNote: null,
        insertableNote: null,
        insertWithDot: false,
        hoveredStaveIdx: null,
        score: new Score(emptyScore),
        activeVoice: {label: "Treble 1", staveIdx: 0, voiceIdx: 0, defaultNote: 'c/5'},
        activeNoteLength: 4,
        interactionMode: 'insert',
        insertionMode: 'note',
        lastInsertionMode: 'rest',
        interactionsInitialised: false,
        dragging: false,
        hasHoveredNote: false,
        isOnCanvas: false,
        autoSelectNewNote: false,
        groupingFraction: new VF.Fraction(2, 8),

        canvasHeight:null,
        canvasWidth:null,
        initialScore:null,
        audioFileUrl:null,
        maxPlayCount:null,
        voiceCount:null,
        canChangeKey:null,
        canChangeTime:null,
      }
    },
    computed: {
      canvasClasses() {
        return {
          'has-highlight': this.hasHoveredNote,
          'is-dragging': !!this.dragging,
          'insertion-mode': this.interactionMode === 'insert',
          'modify-mode': this.interactionMode === 'modify'
        }
      },
      showNotes() {
        return this.insertionMode !== 'rest';
      },
      showRests() {
        return this.insertionMode !== 'note';
      },
      keymap() {
        return {
          '1': () => this.changeNoteLength(1),
          '2': () => this.changeNoteLength(2),
          '3': () => this.changeNoteLength(4),
          '4': () => this.changeNoteLength(8),
          '5': () => this.changeNoteLength(16),
          'del': (ev) => { ev.preventDefault(); this.deleteSelectedNote(); },
          'up': (ev) => { ev.preventDefault(); this.moveNoteUp(); },
          'down': (ev) => { ev.preventDefault(); this.moveNoteDown(); },
          'shift+up': (ev) => { ev.preventDefault(); this.raiseAccidentalForSelectedNote(); },
          'shift+down': (ev) => { ev.preventDefault(); this.lowerAccidentalForSelectedNote(); },
          'left': (ev) => { ev.preventDefault(); this.selectPreviousNote(); },
          'right': (ev) => { ev.preventDefault(); this.selectNextNote(); },
          'esc': (ev) => { ev.preventDefault(); this.deselectNote(); },
          'space': (ev) => { ev.preventDefault(); this.insertDefaultNote(); },
          '.': () => this.toggleDot(),
          'i': () => this.toggleInteractionMode(),
        }
      },
      staveStartX() {
        return 10;
      },
      staveStartY() {
        return 40;
      },
      staveHeight() {
        return 120;
      },
      staveWidth() {
        return this.canvasWidth - 180;
      },
      activeNote() {
        return this.selectedNote ?? {};
      },
      isEmptyStave() {
        return this.score?.isEmpty();
      },
      activeVoiceOptions() {
        return [
          {label: "Soprano", staveIdx: 0, voiceIdx: 0, defaultNote: 'c/5'},
          {label: "Alto", staveIdx: 0, voiceIdx: 1, defaultNote: 'a/4'},
          {label: "Tenor", staveIdx: 1, voiceIdx: 0, defaultNote: 'e/3'},
          {label: "Bass", staveIdx: 1, voiceIdx: 1, defaultNote: 'c/3'},
        ]
      },
      accidentalOptions() {
        return [
          "bb", "b", "n", "#", "##"
        ]
      },
      clefOptions() {
        return [
          {value: "treble", label: "Treble"},
          {value: "bass", label: "Bass"},
        ]
      },
    },
    props: {},
    methods: {
      toggleInteractionMode() {
        if(this.interactionMode === 'insert') {
          this.selectModifyMode();
        } else {
          this.selectInsertMode();
        }
      },
      selectInsertMode() {
        this.interactionMode = 'insert';
        this.deselectNote();
      },
      selectModifyMode() {
        this.interactionMode = 'modify';
        this.selectNextNote();
      },
      toggleDot() {
        if(this.interactionMode === 'insert') {
          this.toggleDotForInsertion();
        } else if(this.interactionMode === 'modify') {
          this.toggleDotForSelectedNote();
        }
      },
      toggleDotForInsertion() {
        this.insertWithDot = !this.insertWithDot;
      },
      toggleAutoSelect() {
        this.autoSelectNewNote = !this.autoSelectNewNote;

        if(!this.autoSelectNewNote) {
          this.deselectNote();
        }
      },
      changeTimeSignature(newTimeSignature, groupingFraction) {
        this.score.time = newTimeSignature;
        this.groupingFraction = groupingFraction;
      },
      changeKeySignature(newKeySignature) {
        this.score.key = newKeySignature;
      },
      changeInsertionMode(newInsertionMode) {
        // if the new insertion mode is the same as current, then toggle back to previous insertion mode
        if(this.insertionMode === newInsertionMode) {
          this.insertionMode = this.lastInsertionMode;
          this.lastInsertionMode = newInsertionMode;
        } else {
          this.lastInsertionMode = this.insertionMode;
          this.insertionMode = newInsertionMode;
        }

        // if changing to rest/note, then change the selected note (shouldn't work for bars)
        if(this.interactionMode === 'modify') {
          if(this.selectedNote?.duration) {
            const newDuration = this.selectedNote.duration.replace('r', '');
            switch(this.insertionMode) {
              case 'rest':
                this.changeSelectedNote({duration: `${newDuration}r`});
                break;
              case 'note':
                this.changeSelectedNote({duration: `${newDuration}`});
                break;
            }
          }
        }
      },
      changeNoteLength(duration, type) {
        this.activeNoteLength = duration;

        if(this.selectedNote) {
          this.changeSelectedNote({duration: duration.toString()});
        }
      },
      selectBar(type) {
        this.activeBarType = type;
        this.lastInsertionMode = this.insertionMode;
        this.insertionMode = 'bar';
      },
      insertDefaultNote() {

      },
      insertNote() {
        if(!this.insertableNote) {
          console.log("cannot insert note, no insertable note");
          return;
        }

        const {staveIdx, voiceIdx, noteIdx} = this.insertableNote;

        let newNote;
        if(this.insertableNote.bar) {
          newNote = this.score.staves[staveIdx].voices[voiceIdx].insertBar(noteIdx ?? 0);
          this.insertionMode = this.lastInsertionMode;
        } else {
          newNote = this.score.staves[staveIdx].voices[voiceIdx].insertNote(noteIdx ?? 0, this.insertableNote);
        }

        if(this.autoSelectNewNote) {
          this.selectNote(staveIdx, voiceIdx, newNote.noteIdx);
        } else {
          this.deselectNote();
        }
      },
      deselectNote() {
        this.selectedNote = null;
        this.mouseDownLoc = null;
        this.hoveredNote = null;
        this.preDragNote = null;
        this.dragNote = null;
      },
      selectNextNote() {
        if(this.selectedNote) {
          const {staveIdx, voiceIdx, noteIdx} = this.selectedNote;
          this.selectNote(staveIdx, voiceIdx, noteIdx + 1);
        } else {
          const staveIdx = this.activeVoice.staveIdx;
          const voiceIdx = this.activeVoice.voiceIdx;

          // select the last note
          if(!this.score.staves[staveIdx].voices[voiceIdx].isEmpty()) {
            this.selectNote(staveIdx, voiceIdx, this.score.staves[staveIdx].voices[voiceIdx].noteCount() - 1);
          }
        }
      },
      selectPreviousNote() {
        if(this.selectedNote) {
          const {staveIdx, voiceIdx, noteIdx} = this.selectedNote;
          this.selectNote(staveIdx, voiceIdx, noteIdx - 1);
        } else {
          const staveIdx = this.activeVoice.staveIdx;
          const voiceIdx = this.activeVoice.voiceIdx;

          // select the first note
          if(!this.score.staves[staveIdx].voices[voiceIdx].isEmpty()) {
            this.selectNote(staveIdx, voiceIdx, 0);
          }
        }
      },
      selectNote(staveIdx, voiceIdx, noteIdx) {
        const vfNote = this.score.getNoteData(staveIdx, voiceIdx, noteIdx);

        if(vfNote) {
          this.selectedNote = vfNote;
          this.interactionMode = 'modify';
        } else {
          this.deselectNote();
        }
      },
      deleteSelectedNote() {
        if(this.selectedNote) {
          const {staveIdx, voiceIdx, noteIdx} = this.selectedNote;
          this.score.staves[staveIdx].voices[voiceIdx].deleteNote(noteIdx);
          this.selectNote(staveIdx, voiceIdx, noteIdx);
        }
      },
      toggleDotForSelectedNote() {
        const {staveIdx, voiceIdx, noteIdx} = this.selectedNote;
        const noteModel = this.score.staves[staveIdx].voices[voiceIdx].notes[noteIdx];
        const currentDottedness = noteModel.dotted;
        noteModel.setDotted(!currentDottedness);
      },
      raiseAccidentalForSelectedNote() {
        if(!this.selectedNote) return;

        const {accidental} = this.selectedNote;
        const accIdx = this.accidentalOptions.indexOf(accidental ?? "n");
        const newAccIdx = Math.min(this.accidentalOptions.length - 1, accIdx + 1);
        this.setAccidentalForSelectedNote(this.accidentalOptions[newAccIdx]);
      },
      lowerAccidentalForSelectedNote() {
        if(!this.selectedNote) return;

        const {accidental} = this.selectedNote;
        const accIdx = this.accidentalOptions.indexOf(accidental ?? "n");
        const newAccIdx = Math.max(0, accIdx - 1);
        this.setAccidentalForSelectedNote(this.accidentalOptions[newAccIdx]);
      },
      setAccidentalForSelectedNote(accidental) {
        if(!this.selectedNote) return;

        const {staveIdx, voiceIdx, noteIdx} = this.selectedNote;
        const currentAccidental = this.score.staves[staveIdx].voices[voiceIdx].notes[noteIdx].accidental;

        if(currentAccidental === accidental) {
          delete this.score.staves[staveIdx].voices[voiceIdx].notes[noteIdx].accidental;
        } else {
          Object.assign(this.score.staves[staveIdx].voices[voiceIdx].notes[noteIdx], {accidental});
        }
        this.selectNote(staveIdx, voiceIdx, noteIdx);
      },
      changeSelectedNote(params){
        if(!this.selectedNote) return;

        const {staveIdx, voiceIdx, noteIdx} = this.selectedNote;
        const voice = this.score.staves[staveIdx].voices[voiceIdx];
        voice.modifyNote(noteIdx, params);
        Object.assign(this.selectedNote, params);
      },
      selectVoice(voiceIdx) {
        this.activeVoice = this.activeVoiceOptions[voiceIdx];
      },
      moveNote(keyIdxOffset) {
        if(!this.selectedNote) return;
        if(this.selectedNote.bar) return;

        const newKeyIdx = this.selectedNote.keyIdx + keyIdxOffset;
        const newKey = keyIdxToKey(newKeyIdx);
        this.changeSelectedNote({keys:[newKey], keyIdx: newKeyIdx});
      },
      moveNoteUp() {
        this.moveNote(1);
      },
      moveNoteDown() {
        this.moveNote(-1);
      },
      //////////////////////////////////////////////////////////////////////////////////////////////////////////////
      onChange() {
        if(this.answerElement) {
          this.answerElement.value = JSON.stringify(this.score);
        }
      },
      buildCursor(clef) {
        const mouseX = this.mouseLoc.x;
        const mouseY = this.mouseLoc.y;

        if(this.insertionMode === 'bar') {
          return this.buildCursorBar(mouseX);
        } else {
          return this.buildCursorNote(clef, mouseX, mouseY);
        }
      },
      insertionDuration() {
        let duration = this.activeNoteLength.toString();
        if(this.insertionMode === 'rest') duration += "r";
        if(this.insertWithDot === true) duration += "d";
        return duration;
      },
      buildCursorNote(clef, mouseX, mouseY) {
        const key = canvasLocToKey(this.staveStartY, mouseY, clef);

        const cursorNote = new VF.StaveNote({
          clef,
          keys: [key],
          duration: this.insertionDuration(),
          auto_stem: true,
        });

        cursorNote.key = key;
        cursorNote.keyIdx = keyToKeyIdx(cursorNote.key);
        cursorNote.leftDisplacedHeadPx = mouseX - 32;

        if(this.insertWithDot) {
          cursorNote.dotted = true;
          cursorNote.addDotToAll();
        }

        return cursorNote;
      },
      buildCursorBar(mouseX) {
        const cursorNote = new VF.BarNote();
        cursorNote.bar = VF.Barline.type.SINGLE;
        cursorNote.leftDisplacedHeadPx = mouseX - 32;
        return cursorNote;
      },
      //////////////////////////////////////////////////////////////////////////////////////////////////////////////
      // this runs after the main score is rendered
      // formatting data is available (note locations, etc.)
      renderOverlay(context, vfStaves, vfVoices) {

        // render the staves, find hovered stave
        this.hoveredStaveIdx = null;
        const staveIdx = 0;
        const clef = this.score.staves[staveIdx].clef;

        vfStaves.forEach((stave, staveIdx) => {
          if (withinBounds(stave.bounds, this.mouseLoc)) {
            this.hoveredStaveIdx = staveIdx;
          }
        });

        this.hasHoveredNote = false;

        if(this.interactionMode === 'modify') {
          vfVoices.forEach((vfVoice, vfVoiceIdx) => {
            const staveIdx = vfVoice.staveIdx;
            const stave = vfStaves[staveIdx];

            vfVoice.tickables.forEach((vfNote, vfNoteIdx) => {
              const x = vfNote.getAbsoluteX();
              const y = stave.bounds.y + vfNote.ys[0] + ((vfNote.stem_direction === VF.StaveNote.STEM_UP) ? 5 : -5) - this.staveStartY;
              const w = vfNote.width;
              const h = (vfNote.stem_direction === VF.StaveNote.STEM_UP) ? -40 : 40;
              const vfNoteBounds = {x, y, w, h};

              const currentNote = this.score.getNoteData(staveIdx, vfVoiceIdx, vfNoteIdx);

              // render hovered indicator
              if (withinBounds(vfNoteBounds, this.mouseLoc)) {
                this.hoveredNote = currentNote;
                this.hasHoveredNote = true;

                // render a tie/slur indicator
                if (this.tieFromNoteIdx === 0 || this.tieFromNoteIdx > 0) {
                  const fromNote = vfVoice.tickables[this.tieFromNoteIdx];
                  const toNote = vfNote;

                  let x1, x2, y1, y2;
                  const yOffset = stave.bounds.y - this.staveStartY;
                  if (fromNote.getAbsoluteX() < toNote.getAbsoluteX()) {
                    x1 = fromNote.getAbsoluteX();
                    x2 = toNote.getAbsoluteX();
                    y1 = fromNote.ys[0] + yOffset;
                    y2 = toNote.ys[0] + yOffset;
                  } else {
                    x1 = toNote.getAbsoluteX();
                    x2 = fromNote.getAbsoluteX();
                    y1 = toNote.ys[0] + yOffset;
                    y2 = fromNote.ys[0] + yOffset;
                  }

                  context.beginPath();
                  context.strokeStyle = "#00000080";
                  context.lineWidth = 3;
                  context.moveTo(x1 + 20, y1);
                  context.lineTo(x2 - 10, y2);
                  context.lineTo(x2 - 16, y2 + 3);
                  context.moveTo(x2 - 10, y2);
                  context.lineTo(x2 - 16, y2 - 3);
                  context.stroke();

                  this.tieToNoteIdx = vfNoteIdx;
                }
              }

              // render selection indicator
              if (vfNoteIdx === this.selectedNote?.noteIdx) {
                if(currentNote.bar) {
                  context.beginPath();
                  context.rect(x-6, y-6, 13, 62);
                  context.fillStyle = "#57c7d420";
                  context.fill();
                  context.strokeStyle = "#57c7d440";
                  context.lineWidth = 4;
                  context.stroke();
                  context.lineWidth = 1;
                } else {
                  const oy = (vfNote.stem_direction === 1) ? (y - 4) : (y + 4);
                  context.beginPath();
                  context.arc(x + 7, oy, 18, 0, 2 * Math.PI);
                  context.fillStyle = "#57c7d420";
                  context.fill();
                  context.strokeStyle = "#57c7d440";
                  context.lineWidth = 4;
                  context.stroke();
                  context.lineWidth = 1;
                }
              }
            });
          });
        }

        if(this.isOnCanvas && this.interactionMode === 'insert') {
          // create a second stave to overlay over the actual stave
          // on this second stave, paint a note to indicate where the cursor is

          context.fillStyle = "#999";
          context.strokeStyle = "#999";

          const scoreModel = this.score;

          const stave = new VF.Stave(this.staveStartX, this.staveStartY + this.staveHeight * staveIdx, this.staveWidth * 1.25);
          stave.context = context;
          let cursorNote = this.buildCursor(clef);

          const voice = new VF.Voice(scoreModel.time);
          voice.setStrict(false);
          voice.addTickables([cursorNote]);
          new VF.Formatter().joinVoices([voice]).format([voice], this.staveWidth);
          voice.draw(context, stave);

          // work out an insertion point
          const tickables = vfVoices[this.activeVoice.voiceIdx].tickables;
          const tickCount = tickables.length;
          const mx = this.mouseLoc.x;
          let insertIdx;

          if(tickCount === 0) {
            insertIdx = -1;
          } else if(mx < tickables[0].getAbsoluteX()) {
            insertIdx = -1;
          } else if(mx > tickables[tickCount - 1].getAbsoluteX()) {
            insertIdx = tickCount;
          } else {
            tickables.forEach((t, idx) => {
              if(mx < t.getAbsoluteX() && !insertIdx) {
                insertIdx = idx;
              }
            });
          }

          this.insertableNote = {
            ...cursorNote,
            staveIdx: this.activeVoice.staveIdx,
            voiceIdx: this.activeVoice.voiceIdx,
            noteIdx: insertIdx,
          }

          if(this.insertionMode !== 'bar') {
            this.insertableNote.duration = this.insertionDuration();
            this.insertableNote.keys = cursorNote.keys;
          }
        } else {
          this.insertableNote = null;
        }
      },
      /////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
      render() {
        const scoreModel = this.score;

        // create a background
        const context = this.rendererContext;
        context.fillStyle = "#f0f0f0";
        context.fillRect(0, 0, this.canvasWidth, this.canvasHeight);
        context.fillStyle = "black";
        context.strokeStyle = "black";

        // map staves from model to VexFlow staves
        const vfStaves = scoreModel.staves.map((staveModel, idx) => {
          const vfStave = new VF.Stave(this.staveStartX, this.staveStartY + this.staveHeight * idx, this.staveWidth * 1.25);
          vfStave.addClef(staveModel.clef);
          vfStave.addTimeSignature(scoreModel.time);
          vfStave.addKeySignature(new VF.KeySignature(scoreModel.key).keySpec);
          vfStave.setEndBarType(VF.Barline.type.END);
          return vfStave;
        });

        // render the staves
        vfStaves.forEach((stave, staveIdx) => {
          stave.bounds.h = (stave.options.num_lines + stave.options.space_above_staff_ln + stave.options.space_below_staff_ln) * stave.options.spacing_between_lines_px;
          stave.setContext(context).draw()
        });

        // create VexFlow voices from score model
        const vfVoices = _.flatten(scoreModel.staves.map(staveModel => staveModel.voices.map(voiceModel => voiceModel.toVFVoice(scoreModel))));

        // Format and justify the notes to fixed width pixels.
        if(!this.isEmptyStave) {
          new VF.Formatter()
            .joinVoices(vfVoices)
            .format(vfVoices, this.staveWidth, {context, stave: vfStaves[0]})

          // auto-beam the notes, and render them
          context.fillStyle = "black";
          context.strokeStyle = "black";
          const allBeams = vfVoices.map(vfVoice => VF.Beam.generateBeams(vfVoice.tickables, !vfVoice.stem_direction ? {
            groups: [this.groupingFraction]
          } : {
            maintain_stem_directions: true,
            stem_direction: vfVoice.stemDirection,
            groups: [this.groupingFraction]
          }));
          vfVoices.forEach(vfVoice => {
            vfVoice.draw(context, vfStaves[vfVoice.staveIdx ?? 0]);
            vfVoice.ties.forEach(tie => tie.setContext(context).draw());
          });
          allBeams.forEach(vfVoiceBeams => vfVoiceBeams.forEach(vfBeam => vfBeam.setContext(context).draw()));

          if(vfVoices[0].ties?.length > 0) {
            vfVoices[0].ties.forEach(tie => {
              tie.setContext(context).draw()
            });
          }
        }

        if (!this.interactionsInitialised) {
          this.setupInteractionLayer();
        }

        this.renderOverlay(context, vfStaves, vfVoices);
        this.onChange();
      },
      /////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
      setupInteractionLayer() {
        const scoreCanvas = document.getElementById("score-canvas");

        scoreCanvas.addEventListener("mouseenter", ev => {
          this.isOnCanvas = true;
        });

        scoreCanvas.addEventListener("mouseleave", ev => {
          this.isOnCanvas = false;
        });

        scoreCanvas.addEventListener("mousemove", ev => {
          this.mouseLoc = { x: ev.offsetX, y: ev.offsetY };
          touchMoved(ev.buttons);
        }, true);

        scoreCanvas.addEventListener("touchmove", ev => {
          this.mouseLoc = targetLocFromTouchEvent(ev);
          touchMoved(1);
          ev.preventDefault();
        }, false);

        const touchMoved = (buttons) => {
          if(!(this.mouseDownLoc && this.mouseLoc)) {
            return;
          }

          // if the drag is less than 10px, then ignore it
          const offsetX = this.mouseDownLoc.x - this.mouseLoc.x;
          const offsetY = this.mouseDownLoc.y - this.mouseLoc.y;
          const distance = Math.sqrt(offsetX * offsetX + offsetY * offsetY);
          if(distance < 10) {
            return;
          }

          this.dragging = buttons > 0;

          this.dragNote = {
            ...this.selectedNote,
          };

          // handle mouse drag
          this.tieFromNoteIdx = null;
          if (this.dragging && this.hoveredNote) {
            const {staveIdx, voiceIdx, noteIdx, keyIdx} = this.dragNote;

            if(Math.abs(offsetX) > 20 && Math.abs(offsetX) > Math.abs(offsetY)) {
              this.score.staves[staveIdx].voices[voiceIdx].modifyNote(noteIdx, {keys: [this.preDragNote.key]});
              this.tieFromNoteIdx = noteIdx;
            } else {
              this.dragNote.offsetX = offsetX;
              this.dragNote.offsetY = offsetY;

              const keyIdxShift = Math.round(offsetY / 5);
              const newKeyIdx = keyIdx + keyIdxShift;
              const newKey = keyIdxToKey(newKeyIdx);

              this.dragNote.keyIdxShift = keyIdxShift;
              this.dragNote.newKeyIdx = newKeyIdx;
              this.dragNote.newKey = newKey;

              this.score.staves[staveIdx].voices[voiceIdx].modifyNote(noteIdx, {keys: [newKey]});
            }
          }
        }

        const touchStart = (loc) => {
          console.log(">>> touchStart", loc)

          this.mouseDownLoc = loc;
          this.mouseLoc = loc;
          this.isOnCanvas = true;

          if(this.hoveredNote) {
            this.selectedNote = {
              ...this.hoveredNote
            };
            this.preDragNote = {
              ...this.hoveredNote
            };
          }
        }

        const touchEnd = (options={}) => {
          console.log(">>> touchEnd")
          if(!this.dragging && this.insertableNote) {
            this.insertNote();
          }

          if((this.tieFromNoteIdx === 0 || this.tieFromNoteIdx) && this.tieToNoteIdx) {
            const {staveIdx, voiceIdx} = this.preDragNote;
            this.score.staves[staveIdx].voices[voiceIdx].createTie(this.tieFromNoteIdx, this.tieToNoteIdx);
          }

          this.dragging = false;
          this.isOnCanvas = !options.removeFromCanvas;
        }

        scoreCanvas.addEventListener("touchstart", ev => {
          ev.preventDefault();
          touchStart(targetLocFromTouchEvent(ev));
        }, false);

        scoreCanvas.addEventListener("mousedown", ev => {
          touchStart({ x: ev.offsetX, y: ev.offsetY });
          ev.preventDefault();
        }, false);

        scoreCanvas.addEventListener("touchend", ev => {
          touchEnd({removeFromCanvas:true});
          ev.preventDefault();
        }, false);

        scoreCanvas.addEventListener("mouseup", ev => {
          touchEnd({removeFromCanvas:false});
          ev.preventDefault();
        }, false);

        this.interactionsInitialised = true;
      },
    },
    watch: {
      answerElementId: function (newVal, oldVal) {
        this.answerElement = document.getElementById(newVal);
      },
      canvasHeight: function(newVal) {
        this.renderer.resize(this.canvasWidth, newVal);
      },
      canvasWidth: function(newVal) {
        this.renderer.resize(newVal, this.canvasHeight);
      },
      initialScore: function(newVal) {
        this.score = new Score(newVal);
      },
      selectedNote: function(newVal) {
        if(newVal) {
          this.activeNoteLength = parseInt(newVal.duration);

          if(newVal.duration?.includes('r')) {
            this.insertionMode = 'rest';
          } else if(newVal.bar) {
            this.insertionMode = 'bar';
          } else {
            this.insertionMode = 'note';
          }
        }
      }
    },
    mounted() {
      const scoreDiv = document.getElementById("score-canvas");
      scoreDiv.innerHTML = "";
      this.renderer = new VF.Renderer(scoreDiv, VF.Renderer.Backends.CANVAS);
      this.renderer.resize(this.canvasWidth, this.canvasHeight);
      this.rendererContext = this.renderer.getContext();

      this.mouseLoc = { x:this.canvasWidth/2, y:this.canvasHeight/2 }

      setInterval(this.render, 20);
    },
  };
</script>

