<template>
  <div>
    <button type="button"
            v-for="(voice, voiceIdx) of voices"
            @click="selectVoice(voiceIdx)"
            :class="{
              selected: selectedVoiceIdx === voiceIdx,
              left: parseInt(voiceIdx) === 0,
              right: voiceIdx === voiceCount - 1
            }"
            class="switch-button">{{ voice }}</button>
  </div>
</template>

<script>
  import {VOICE_NAMES} from "../constants";

  export default {
    name: "VoiceSelector",
    props: ['voiceCount'],
    data() {
      return {
        selectedVoiceIdx: 0
      }
    },
    computed: {
      voices() {
        return VOICE_NAMES.slice(0, this.voiceCount);
      }
    },
    methods: {
      selectVoice(idx) {
        this.selectedVoiceIdx = idx;
        this.$emit("change", idx);
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../style/main";

  .switch-button {
    border: solid 3px $button1;
    border-right: 0;
    background-color: $button2;
    color: $button-fg;
    padding: 1rem;

    &.selected {
      background-color: $button2alt;
    }

    &.left {
      border-top-left-radius: 0.5rem;
      border-bottom-left-radius: 0.5rem;
    }

    &.right {
      border-top-right-radius: 0.5rem;
      border-bottom-right-radius: 0.5rem;
      border-right: solid 3px $button1;
    }
  }

</style>
