<template>
  <div v-hotkey="keymap">
    <button type="button"  title="(-) Previous key" @click="selectPreviousKey" class="icon-button left"><IconRemove /></button>
    <span class="accidental-count">{{ accidentalCount }}</span>
    <button type="button"  title="(=) Next key" @click="selectNextKey" class="icon-button right"><IconAdd /></button>
  </div>
</template>

<script>

import IconRemove from '../assets/icon_remove.svg';
import IconAdd from '../assets/icon_add.svg';
import {KEY_SIGNATURES} from "../constants";

export default {
  props: ['key-signature'],
  data() {
    return {
      keySignatureIdx: 0
    }
  },
  components: {
    IconAdd,
    IconRemove
  },
  computed: {
    accidentalCount() {
      if(this.keySignatureIdx <= 6) {
        return `${this.keySignatureIdx}`;
      } else {
        return `${KEY_SIGNATURES.length - this.keySignatureIdx}`;
      }
    },
    keyOptions() {
      return KEY_SIGNATURES;
    },
    keymap() {
      return {
        '=': () => this.selectNextKey(),
        '-': () => this.selectPreviousKey(),
      }
    },
  },
  methods: {
    selectNextKey() {
      this.keySignatureIdx = (this.keySignatureIdx + 1) % this.keyOptions.length;
      this.$emit('change', this.keyOptions[this.keySignatureIdx]);
    },
    selectPreviousKey() {
      this.keySignatureIdx = (this.keySignatureIdx - 1 + this.keyOptions.length) % this.keyOptions.length;
      this.$emit('change', this.keyOptions[this.keySignatureIdx]);
    },
  },
  mounted() {
    this.keySignatureIdx = this.keyOptions.indexOf(this.keySignature);
  },
}
</script>

<style lang="scss" scoped>

  .accidental-count {
    padding: 0.5rem;
  }

</style>
