import Vex from 'vexflow';
import {keyToKeyIdx} from "./utils";
const VF = Vex.Flow

export class Score {
  constructor(props) {
    this.time = props.time;
    this.key = props.key;
    this.barCount = props.barCount;
    this.trebleVoiceCount = props.trebleVoiceCount ?? 1;
    this.bassVoiceCount = props.bassVoiceCount ?? 0;
    this.staves = Stave.fromList(props.staves, this);
    this.strict = false;
  }

  toJSON() {
    return _.omit(this, ['strict']);
  }

  isEmpty() {
    return this.staves.every(s => s.isEmpty());
  }

  getNoteData(staveIdx, voiceIdx, noteIdx) {
    if(staveIdx >= this.staves.length) {
      return null;
    }

    if(voiceIdx >= this.staves[staveIdx].voices.length) {
      return null;
    }

    let vfNoteIdx;
    if(noteIdx <= 0) {
      vfNoteIdx = 0;
    } else if(noteIdx >= this.staves[staveIdx].voices[voiceIdx].notes.length) {
      vfNoteIdx = this.staves[staveIdx].voices[voiceIdx].notes.length - 1;
    } else {
      vfNoteIdx = noteIdx;
    }

    const vfNote = this.staves[staveIdx].voices[voiceIdx].notes[vfNoteIdx];

    if(!vfNote) {
      return null;
    }

    if(vfNote.bar) {
      return {
        staveIdx: staveIdx,
        voiceIdx: voiceIdx,
        noteIdx: vfNoteIdx,
        bar: vfNote.bar,
      }
    }

    return {
      staveIdx: staveIdx,
      voiceIdx: voiceIdx,
      noteIdx: vfNoteIdx,
      keyIdx: keyToKeyIdx(vfNote.keys[0]),
      key: vfNote.keys[0],
      duration: vfNote.duration,
      accidental: vfNote.accidental,
      dotted: vfNote.dotted,
    };
  }
}

export class Stave {
  constructor(props, score) {
    this.clef = props.clef;
    this.voices = Voice.fromList(props.voices ?? [], this);
    this.score = score;
  }

  toJSON() {
    return _.omit(this, ['score', 'index']);
  }

  static fromList(staves, score) {
    return staves.map((s, idx) => {
      const stave = new Stave(s, score);
      stave.index = idx;
      return stave;
    });
  }

  isEmpty() {
    return this.voices.every(v => v.isEmpty());
  }
}

export class Voice {
  constructor(props, stave) {
    this.stemDirection = props.stemDirection;
    this.notes = Note.fromList(props.notes ?? [], this);
    this.ties = Tie.fromList(props.ties ?? [], this);
    this.stave = stave;
  }

  noteCount() {
    return this.notes?.length ?? 0;
  }

  isEmpty() {
    return !(this.notes?.length > 0);
  }

  createTie(startNoteIdx, endNoteIdx) {
    if(startNoteIdx === endNoteIdx) {
      return;
    }

    const tie = new Tie({
      startNoteIdx: Math.min(startNoteIdx, endNoteIdx),
      endNoteIdx: Math.max(startNoteIdx, endNoteIdx),
    });

    this.ties.push(tie);
  }

  modifyNote(idx, params) {
    Object.assign(this.notes[idx], params);
  }

  deleteNote(idx) {
    this.notes.splice(idx, 1);

    // remove any ties this note is involved in
    this.ties = this.ties
      .filter(tie => !tie.includes(idx));

    // move any ties after this note forward by 1 note
    this.ties
      .filter(tie => tie.startNoteIdx > idx)
      .forEach(tie => {
        tie.startNoteIdx = tie.startNoteIdx - 1;
        tie.endNoteIdx = tie.endNoteIdx - 1;
      });
  }

  insertNote(idx, note) {
    return this._insertNote(idx, new Note(note));
  }

  insertBar(idx, type='single') {
    return this._insertNote(idx, new BarNote({bar:type}));
  }

  _insertNote(idx, note) {
    if(!this.notes?.length) {
      this.notes = [note];
      note.noteIdx = 0;
      return note;
    }

    if(idx >= this.notes.length) {
      note.noteIdx = this.notes.length;
      this.notes.push(note);
      return note;
    }

    if(idx <= 0) {
      note.noteIdx = 0;
      this.notes.unshift(note);
      return note;
    }

    note.noteIdx = idx;
    this.notes.splice(idx, 0, note);

    // remove any ties that this may affect
    this.ties = this.ties.filter(tie => !tie.contains(idx));

    return note;
  }

  toJSON() {
    return _.omit(this, ['stave', 'index']);
  }

  static fromList(voices, stave) {
    return voices.map((v, idx) => {
      if(voices.length > 1) {
        v.stemDirection = (idx === 0) ? 1 : -1;
      } else {
        v.stemDirection = 0;
      }

      const voice = new Voice(v, stave);
      voice.index = idx;
      return voice;
    });
  }

  toVFVoice(score) {
    const vfVoice = new VF.Voice(score.time);
    vfVoice.setStrict(score.strict);
    vfVoice.addTickables(this.notes.map(n => n.toVFNote(this)));
    vfVoice.voiceIdx = this.index;
    vfVoice.staveIdx = this.stave.index;
    vfVoice.ties = (this.ties || []).map(tie => tie.toVFStaveTie(vfVoice));
    return vfVoice;
  }
}

export class Tie {
  constructor(props, voice) {
    this.startNoteIdx = props.startNoteIdx;
    this.endNoteIdx = props.endNoteIdx;
    this.voice = voice;
  }

  toJSON() {
    return _.omit(this, ['voice']);
  }

  includes(idx) {
    return this.startNoteIdx === idx || this.contains(idx);
  }

  contains(idx) {
    return (idx > this.startNoteIdx && idx <= this.endNoteIdx);
  }

  static fromList(ties, voice) {
    return ties.map(tie => new Tie(tie, voice));
  }

  toVFStaveTie(vfVoice) {
    return new VF.StaveTie({
      first_note: vfVoice.tickables[this.startNoteIdx],
      last_note: vfVoice.tickables[this.endNoteIdx],
      first_indices: [0],
      last_indices: [0],
    });
  }
}

export class Note {
  constructor(props, voice) {
    this.keys = props.keys;
    this.duration = props.duration;
    this.dotted = props.dotted ?? false;
    this.accidental = props.accidental;
    this.voice = voice;
  }

  toJSON() {
    const json = _.omit(this, ['voice', 'index', 'noteIdx', 'keyIdx']);

    if(!json.dotted) {
      delete json.dotted;
    }

    return json;
  }

  setDotted(state) {
    this.duration = this.duration.replace('d', '');

    if(state) {
      this.duration = this.duration + 'd';
      this.dotted = true;
    } else {
      delete this.dotted;
    }
  }

  static fromList(notes, voice) {
    return notes.map((n,idx) => {
      if(n.bar) {
        return new BarNote(n, voice);
      }

      const note = new Note(n, voice);
      note.index = idx;
      return note;
    });
  }

  toVFNote(voice) {
    const vfNote = new VF.StaveNote({
      clef: voice.stave.clef,
      keys: this.keys,
      duration: this.dotted ? `${this.duration}d` : `${this.duration}`,
      auto_stem: true
    });

    if(!voice.stemDirection) {
      vfNote.auto_stem = true;
    } else {
      vfNote.stem_direction = voice.stemDirection;
    }

    if (this.dotted) {
      vfNote.addDotToAll();
    }

    if (this.accidental) {
      vfNote.addAccidental(0, new VF.Accidental(this.accidental))
    }

    return vfNote;
  }
}

export class BarNote {
  constructor(props, voice) {
    this.bar = props.bar;
    this.voice = voice;
  }

  toJSON() {
    return _.pick(this, ['bar']);
  }

  toVFNote(voice) {
    return new VF.BarNote(VF.Barline.type[this.bar.toUpperCase()]);
  }
}
