<template>
  <div class="player">
    <div class="play-button">
      <button type="button"
              @click="playAudio"
              :disabled="playDisabled">
        <IconPlay />
      </button>
    </div>
    <div class="seeker">
      <audio ref="audio" :src="src" />
      {{ state }}<br/>
      {{ displayProgress }}
    </div>
    <div class="play-counter">
      <button
        type="button">
        {{ playCounter }}
      </button>
    </div>
  </div>
</template>

<script>

import IconPlay from '../assets/icon_play.svg';
import {formatTime} from "../utils";

export default {
  name: "AudioPlayer",
  props: ['src', 'maxPlayCount'],
  components: {
    IconPlay
  },
  data() {
    return {
      state: "loading",
      playCounter: 3,
      currentTime: 0,
      duration: 0,
    }
  },
  methods: {
    playAudio() {
      this.audio.play();
    },
    formatTime(val) {
      return formatTime(val);
    }
  },
  computed: {
    playDisabled() {
      return this.state === 'playing' || this.playCounter <= 0;
    },
    displayProgress() {
      return `${ this.formatTime(this.currentTime || 0) } / ${ this.formatTime(this.duration) }`
    }
  },
  watch: {
    maxPlayCount: function(newVal) {
      this.playCounter = newVal;
    }
  },
  mounted() {
    this.audio = this.$refs.audio;

    this.audio.addEventListener("timeupdate", () => {
      this.currentTime = this.audio.currentTime;
    });

    this.audio.addEventListener("play", () => {
      this.state = 'playing';
      this.playCounter -= 1;
    });

    this.audio.addEventListener("ended", () => {
      this.state = 'ready';
    });

    this.audio.addEventListener("durationchange", () => {
      this.duration = this.audio.duration;
    });

    this.audio.addEventListener("canplaythrough", () => {
      this.state = 'ready';
    });
  }
}
</script>

