import Vue from 'vue';
import App from './AppCanvas.vue';
import VueHotkey from 'v-hotkey'

Vue.use(VueHotkey)

Vue.config.productionTip = false;

const appElementId = "app";
const appElement = document.getElementById(appElementId);

const answerElementId = appElement.getAttribute("data-answer-element-id");
const maxPlayCount = appElement.getAttribute("data-max-play-count");
const audioFileUrl = appElement.getAttribute("data-audio-file-url");
const initialScore = appElement.getAttribute("data-initial-score");
const canvasHeight = appElement.getAttribute("data-canvas-height");
const canvasWidth = appElement.getAttribute("data-canvas-width");
const voiceCount = appElement.getAttribute("data-voice-count");
const canChangeKey = appElement.getAttribute("data-can-change-key");
const canChangeTime = appElement.getAttribute("data-can-change-time");

const vm = new Vue({
  render: (h) => h(App),
}).$mount(`#${appElementId}`);

vm.$children[0].$data.answerElementId = answerElementId;
vm.$children[0].$data.maxPlayCount = parseInt(maxPlayCount ?? 2);
vm.$children[0].$data.audioFileUrl = atob(audioFileUrl);
vm.$children[0].$data.initialScore = JSON.parse(atob(initialScore ?? btoa("{}")));
vm.$children[0].$data.canvasHeight = parseInt(canvasHeight ?? 200);
vm.$children[0].$data.canvasWidth = parseInt(canvasWidth ?? 600);
vm.$children[0].$data.voiceCount = parseInt(voiceCount ?? 1);
vm.$children[0].$data.canChangeKey = parseInt(canChangeKey ?? 1);
vm.$children[0].$data.canChangeTime = parseInt(canChangeTime ?? 1);
