<template>
  <div class="time-selector">
    <vue-select v-model="numerator" :options="numeratorOptions" :clearable="false" @input="setDenominator" />
    <vue-select v-model="denominator" :options="denominatorOptions" :clearable="false" @input="setNumerator" />
  </div>
</template>

<script>
import VueSelect from "vue-select";
import 'vue-select/dist/vue-select.css';
import {TIME_DENOMINATOR_OPTIONS, TIME_NUMERATOR_OPTIONS} from "../constants";
import Vex from 'vexflow';

  export default {
    name: "TimeSelector",
    props: ['time-signature'],
    components: {
      VueSelect
    },
    data() {
      return {
        numerator: 0,
        denominator: 0,
      }
    },
    methods: {
      setDenominator(newVal) {
        this.$emit('change', `${newVal}/${this.denominator}`, this.groupingFraction);
      },
      setNumerator(newVal) {
        this.$emit('change', `${this.numerator}/${newVal}`, this.groupingFraction);
      }
    },
    computed: {
      isCompoundTime() {
        return (this.numerator % 3 === 0) && (this.numerator !== 3);
      },
      groupingFraction() {
        return this.isCompoundTime ? new Vex.Flow.Fraction(3, 8) : new Vex.Flow.Fraction(2, 8);
      },
      numeratorOptions() {
        return TIME_NUMERATOR_OPTIONS;
      },
      denominatorOptions() {
        return TIME_DENOMINATOR_OPTIONS;
      },
    },
    mounted() {
      this.numerator = parseInt(this.timeSignature.split('/')[0]);
      this.denominator = parseInt(this.timeSignature.split('/')[1]);
      this.$emit('change', `${this.numerator}/${this.denominator}`, this.groupingFraction);
    }
  }

</script>

<style lang="scss">
  .time-selector {
    display: grid;
    grid-template-columns: 45% 45%;
  }
</style>
