export function withinBounds(bounds, coords = {x: 0, y: 0}) {

  const x0 = Math.min(bounds.x, bounds.x + bounds.w);
  const x1 = Math.max(bounds.x, bounds.x + bounds.w);
  const y0 = Math.min(bounds.y, bounds.y + bounds.h);
  const y1 = Math.max(bounds.y, bounds.y + bounds.h);

  if (coords.x < x0) return false;
  if (coords.x > x1) return false;
  if (coords.y < y0) return false;
  if (coords.y > y1) return false;

  return true;
}

export function keyToKeyIdx(key) {
  if(!key) {
    return null;
  }

  const [note, octave] = key.split("/");
  const noteIdx = ((note.toLowerCase().charCodeAt(0) - 97) + 7 - 2) % 7;
  const octaveIdx = parseInt(octave) - 1;
  return octaveIdx * 7 + noteIdx;
}

export function keyIdxToKey(idx) {
  // c1 => index 0, c2 => index 7, ..., e4 => 23, etc...
  if(!idx && idx !== 0) {
    throw new Error(`no key index specified (given ${idx})`);
  }

  const key = String.fromCharCode(97 + ((idx + 2) % 7));
  const octave = Math.floor(idx / 7) + 1;
  return `${key}/${octave}`;
}

export function canvasLocToKey(staveStartY, canvasLocY, clef = 'treble') {
  const staveY = canvasLocY - staveStartY;
  const topLineKey = (clef === 'treble') ? 'g/6' : 'b/4';
  const topLineKeyIdx = keyToKeyIdx(topLineKey);
  const maxKeyIdx = topLineKeyIdx;
  const minKeyIdx = topLineKeyIdx - 24;
  const keyIdx = topLineKeyIdx - Math.round(staveY / 5.0);
  return keyIdxToKey(Math.max(Math.min(keyIdx, maxKeyIdx), minKeyIdx));
}


export function targetLocFromTouchEvent(ev) {
  const offsetX = ev.target.getBoundingClientRect().x + window.scrollX;
  const offsetY = ev.target.getBoundingClientRect().y + window.scrollY;
  const touchX = ev.targetTouches[0].pageX;
  const touchY = ev.targetTouches[0].pageY;
  const x = touchX - offsetX;
  const y = touchY - offsetY;
  return { x, y };
}

export function formatTime(val) {
  const mins = Math.floor(val / 60);
  const secs = Math.floor(val % 60);
  return `${mins}:${secs < 10 ? '0' : ''}${secs}`;
}
